<template>
    <form @submit.prevent="authUser">
       
        <b-form-input
          id="input-1"
          v-model.trim="login"
          type="text"
          required
          placeholder="Ваш логин"
        ></b-form-input>
        <p v-if="$v.login.$dirty && !$v.login.required">Поле Login не должно быть пустым</p>
        <p v-if="$v.login.$dirty && !$v.login.minLength">Поле Login  должно быть больше 6 символов. Сейчас {{login.length}}</p>
        <p v-if="$v.login.$dirty && !$v.login.maxLength">Поле Login  должно быть меньше 20 символов. Сейчас {{login.length}}</p>
        <b-form-input
          id="input-1"
          v-model.trim="email"
          type="email"
          required
          placeholder="Ваша почта"
        ></b-form-input>
        <p v-if="$v.email.$dirty && !$v.email.required">Поле email не должно быть пустым</p>
        <p v-if="$v.email.$dirty && !$v.email.minLength">Поле email  должно быть больше 6 символов. Сейчас {{email.length}}</p>
        <p v-if="$v.email.$dirty && !$v.email.maxLength">Поле email  должно быть меньше 20 символов. Сейчас {{email.length}}</p>
        <p v-if="$v.email.$dirty && !$v.email.email">Email введен не корректно</p>
        <b-form-input
          id="input-1"
          v-model="pass"
          type="password"
          required
          placeholder="Ваш пароль"
        ></b-form-input>
        <p v-if="$v.pass.$dirty && !$v.pass.required">Поле pass не должно быть пустым</p>
        <p v-if="$v.pass.$dirty && !$v.pass.minLength">Поле pass  должно быть больше 6 символов. Сейчас {{pass.length}}</p>
        <p v-if="$v.pass.$dirty && !$v.pass.maxLength">Поле pass  должно быть меньше 20 символов. Сейчас {{pass.length}}</p>
        <b-button type="submit" variant="primary">Регистрация</b-button>
        {{getRegStatus}}
    </form>



</template>



<script>

import {mapActions, mapGetters} from "vuex";

import {required, minLength, maxLength, email} from "vuelidate/lib/validators"

export default {
    data(){
        return {
            login: "",
            pass: "",   
            email: "",
        }
    },
     validations: {
        email: {required, minLength: minLength(6), maxLength: maxLength(20), email},
        login: {required, minLength: minLength(6), maxLength: maxLength(20)},
        pass: {required, minLength: minLength(6) , maxLength: maxLength(20)}
    },
    methods: {
        ...mapActions([
            "userReg"
        ]),
        authUser() {
            if(this.$v.$invalid){
                this.$v.$touch()
                return
            }
            this.userReg({login: this.login, pass: this.pass, email: this.email});
            //this.$store.dispatch("checkStatus");
        }
    },
    computed: mapGetters([
        "getRegStatus"
    ]),
}
</script>

<style lang="scss" scoped>
    form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    input, button {
        margin-top: 20px;
        max-width: 500px;
        
    }
</style>